html,
body {
  height: 100%;
  min-height: 100vh;
}
body {
  background: #F8FAFC;
}
.header {
  width: 100%;
  background: #1E293B;
  padding: 16px 24px;
  border-radius: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-title {
  color: white;
  font-size: 20px;
  font-weight: 600;
}
.btn-primary {
  background: #2563EB;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
}
.btn-primary:hover {
  background: #3B82F6;
}
.kanban {
  display: flex;
  gap: 24px;
  margin-top: 24px;
}
.column {
  width: 280px;
  background: #E6EEF8;
  padding: 16px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.column-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 4px;
}
.card {
  background: #FFFFFF;
  padding: 16px;
  border-radius: 10px;
  border: 1px solid #E5E7EB;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  transition: 0.15s ease;
  color: #0F172A;
}
.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(15, 23, 42, 0.12);
}
.card.active {
  background: #BFDBFE;
  border-color: #93C5FD;
  box-shadow: 0 10px 30px rgba(15, 23, 42, 0.12);
}
.card.dragging {
  transform: scale(1.03);
  opacity: 0.9;
  box-shadow: 0 16px 40px rgba(15, 23, 42, 0.2);
}
.task-detail-modal {
  background-color: #FFFFFF;
  border-radius: 12px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  border: 1px solid #E2E8F0;
  max-width: 32rem;
  width: 100%;
  padding: 32px;
}
.modal-close-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 24px;
  height: 24px;
  border-radius: 9999px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #EF4444;
  border: 1px solid white;
  cursor: pointer;
  z-index: 10;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.modal-close-btn:hover {
  background-color: #DC2626;
}
